@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #181818;
  background-blend-mode: overlay;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  -webkit-backdrop-filter: blur(100px);
          backdrop-filter: blur(100px);
  overflow: hidden;
  /* overscroll-behavior: none; */
  color: white;
}

h1 {
  font-weight: 800;
}

img {
  -webkit-user-drag: none;
}

.sll-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 4em;
  padding-left: 2em;
  padding-right: 2em;
}

.sll-container-children {
  width: 60em;
  max-width: 60em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.sll-player-background {
  min-height: 100vh;
}

.sll-player-meta {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.sll-player-cover {
  width: 150px;
  height: 150px;
  border-radius: 10px;
  box-shadow: 5px 5px 15px 4px rgba(0, 0, 0, 0.15);
}

.sll-player-song {
  margin-left: 2em;
}

.sll-player-title {
  margin-top: 16px;
  font-size: 30px;
  font-weight: bold;
}

.sll-player-artist {
  margin-top: -16px;
}

.sll-player-lyrics {
  height: 72vh;
  overflow: scroll;
  margin-top: 1em;
  white-space: pre-line;
  font-weight: bold;
  font-size: 25px;
}

.sll-button {
  margin-top: 1em;
  padding-left: 3em;
  padding-right: 3em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 3.5em;
  border-radius: 25px;
  border: none;
  background-color: #1cb955;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

.sll-button:hover {
  cursor: pointer;
  background-color: #1bd760;
}

.sll-large-text {
  font-weight: normal;
  white-space: pre-line;
}

